.search {
  display: flex;
  align-items: center;
  position: relative;
  flex: 1;
}

.search__input {
  width: 100%;
  border-radius: 24px;
  border: 1px solid transparent;
  outline: none;
  padding: 13px 60px 13px 20px;
  color: #000;
  font-size: 16px;
  line-height: 20px;
  caret-color: #feba0c;
}
.search__input:focus {
  background: #ffffff;
  border: 1px solid #fed700;
}

.search__input::placeholder {
  color: #7b8e98;
}

.search__btn {
  background-color: transparent;
  outline: none;
  border: none;
  padding: 0;
  cursor: pointer;
  position: absolute;
  right: 20px;
  display: flex;
}
.search__btn svg {
  fill: #7b8e98;
}
.search__btn_disabled svg {
  fill: #cfd8dc;
}
.search__icon-clear:focus,
.search__icon:focus {
  fill: #feba0c;
}
.search__icon-clear:hover,
.search__icon:hover {
  fill: #feba0c;
}

@media (max-width: 350px) {
  .search__input {
    max-width: 400px;
  }
}
